.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  width: 100%;

  &_form {
    box-shadow: var(--default-box-shadow);
    border-radius: 28px;
    padding: 28px;

    min-height: 600px;
    max-width: 600px;
    min-width: 300px;
  }

  &_link {
    text-decoration: none;
    
    &:hover,
    &:visited {
      text-decoration: none;
    }
  }
}

.link_container {
  display: flex;
  justify-content: space-evenly;

  margin-top: 40px;
  width: 100%;
}