.password {
  position: relative;

  &_input {
    box-sizing: border-box;
  
    background-color: var(--input-back-color);
    border: {
      color: var(--on-element-color);
      width: 2px;
      style: solid;
    }
  
    height: 2rem;
    width: calc(100% - 2rem);
  }
}

.switch {
  display: flex;
  box-sizing: border-box;
  position: absolute;
  top: 1.5rem;
  right: 0;
  justify-content: center;
  align-items: center;
  background-color: var(--input-back-color);
  border: {
    color: var(--on-element-color);
    width: 2px;
    style: solid;
  }

  width: 2rem;
  height: 2rem;
  
  &_input {
    display: none;
  }
}
